export default {
    created(){
     
    },
    data() {
        return {
            value: "",
            showfeelback: false,






           
        }
    },
    methods: {
        async post() {
            if (this.value.length == 0) {
                uni.showToast({
                    title: "说点什么吧...",
                    icon: "none"
                })
                return;
            }
            if (this.value.length <= 5) {
                uni.showToast({
                    title: "内容太少了",
                    icon: "none"
                })
                return;
            }
            await this.$u.post('/feedback', {
                feedback_content: this.value
            }).then(res => {
                if (res.errorCode !== 0) return;
                uni.showToast({
                    title: '感谢您的反馈'
                })
                return;
            })
        },
        async update(v) {





































           
